import './AdminCliente.css'

import {
  IonAlert,
  IonCheckbox,
  IonContent,
  IonHeader,
  IonImg,
  IonInput,
  IonItem,
  IonLabel,
  IonList,
  IonModal,
  IonSelect,
  IonSelectOption,
  IonTitle,
  IonToolbar,
} from '@ionic/react'
import Tree, { NodeId } from '@naisutech/react-tree'
import { observer } from 'mobx-react-lite'
import React, { useState, useEffect } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import Uppy from '@uppy/core'
import { FileInput } from '@uppy/react'
import Webcam from '@uppy/webcam'

import { Page } from '../../components'
import { RUTAS } from '../../constants'
import {useApiToaster, useOpenPage, useToaster} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Categoria, CategoriaConfig } from '../../services/api'

const ImageCompressor = require('uppy-plugin-image-compressor')

const Espanol = require('@uppy/locales/lib/es_ES')

const XHRUpload = require('@uppy/xhr-upload')

const estiloArbolCategorias = {
  estilo: {
    text: '#000000',
    bg: '#ffffff',
    highlight: '#f4f4f4',
    decal: '#303AB2',
    indicator: '#303AB2',
    separator: '#303AB2',
    icon: '#303AB2',
    selectedBg: '#303AB2',
    selectedText: '#fff',
    hoverBg: '#505a63', // background of hovered element
    accent: '#999',
    hoverText: '#fafafa', // text color of hovered element
    accentBg: '#2d3439', // background of empty folder element
    accentText: '#999', // text color of empty folder element
    textSize: 'large', // preferred text size
  },
}

export const AdminCategoria: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore

  const [CategoriasTree, setCategoriasTree] = useState<any[]>([])
  const [CategoriasTreeMostrar, setCategoriasTreeMostrar] = useState<any[]>([])
  const [Loading, setLoading] = useState(false)
  const [ModalCargaImagen, setModalCargaImagen] = useState(false)
  const [showModal, setShowModal] = useState(false)
  const [CategoriaNombre, setCategoriaNombre] = useState<string>()
  const [CategoriaComision, setCategoriaComision] = useState<number>()
  const [CategoriaNoAptoMenores, setCategoriaNoAptoMenores] = useState<boolean>(false)
  const [CategoriaId, setCategoriaId] = useState<number | null>()
  const [CategoriaImagen, setCategoriaImagen] = useState<string | null>()
  const [CategoriaIcono, setCategoriaIcono] = useState<string | null>()
  const [CategoriaEliminarId, setCategoriaEliminarId] = useState<number | null>()
  const [CategoriaEliminarImagen, setCategoriaEliminarImagen] = useState(false)
  const [CategoriaEliminarIcono, setCategoriaEliminarIcono] = useState(false)
  const [CategoriaPadreId, setCategoriaPadreId] = useState<number | null>()
  const [showAlert, setShowAlert] = useState(false)
  const [CategoriasList, setCategoriasList] = useState<Categoria[]>([])
  const [CategoriaTipoIva, setCategoriaTipoIva] = useState<number>()

  const apiToaster = useApiToaster()
  const toaster = useToaster()
  const openPage = useOpenPage()

  const tiposIva = [
    {"value": 10, "label": "IVA 5"},
    {"value": 20, "label": "IVA 10"},
    {"value": 30, "label": "Exenta"},
  ]
  const setup = async () => {
    obtenerConfiguracionArbol(null)
  }

  React.useEffect(() => {
    setup()
  }, [])

  const uppy = React.useMemo(() => {
    const categoria_id = Math.abs(CategoriaId || 0)
    return Uppy({
      restrictions: { allowedFileTypes: ['image/*'], maxNumberOfFiles: 1, minNumberOfFiles: 1 },
      meta: {
        categoria: categoria_id,
      },
      locale: Espanol,
    })
      .use(Webcam)
      .use(XHRUpload, {
        endpoint: `${rootStore.environment.api.config.url}/categoria/${categoria_id}/`,
        headers: {
          Authorization: 'Token ' + loginStore.token,
        },
        method: 'PATCH',
        formData: true,
        fieldName: 'imagen',
      })
      .use(ImageCompressor, {
        maxHeight: 2000,
        maxWidth: 2000,
        quality: 0.7,
      })
  }, [CategoriaId])

  useEffect(() => {
    uppy.on('complete', (result: any) => {
      if (result.successful.length > 0) {
        const sresult = result.successful[0]
        // Actualizar categoria
        const categoriasTree = [...CategoriasTree]
        const categoriaSeleccionada = categoriasTree.find((cat) => cat.id === sresult.meta.categoria)
        categoriaSeleccionada.imagen = sresult.response.body.imagen
        setCategoriasTree(categoriasTree)
        //
        setCategoriaId(0)
      }
    })

    uppy.on('error', (result: any) => {
      toaster('Error al subir imagen de la categoría')
    })

    uppy.on('file-added', (file: any) => {
      const reader = new FileReader()
      reader.readAsDataURL(file.data)
      reader.onload = function () {
        setCategoriaImagen(reader.result as string)
      }
    })

    return () => uppy.close()
  }, [uppy])

  const uppyIcon = React.useMemo(() => {
    const categoria_id = Math.abs(CategoriaId || 0)
    return Uppy({
      restrictions: { allowedFileTypes: ['image/*'], maxNumberOfFiles: 1, minNumberOfFiles: 1 },
      meta: {
        categoria: categoria_id,
      },
      locale: Espanol,
    })
        .use(Webcam)
        .use(XHRUpload, {
          endpoint: `${rootStore.environment.api.config.url}/categoria/${categoria_id}/`,
          headers: {
            Authorization: 'Token ' + loginStore.token,
          },
          method: 'PATCH',
          formData: true,
          fieldName: 'icono',
        })
        .use(ImageCompressor, {
          maxHeight: 2000,
          maxWidth: 2000,
          quality: 0.7,
        })
  }, [CategoriaId])

  useEffect(() => {
    uppyIcon.on('complete', (result: any) => {
      if (result.successful.length > 0) {
        const sresult = result.successful[0]
        // Actualizar categoria
        const categoriasTree = [...CategoriasTree]
        const categoriaSeleccionada = categoriasTree.find((cat) => cat.id === sresult.meta.categoria)
        categoriaSeleccionada.icono = sresult.response.body.icono
        setCategoriasTree(categoriasTree)
        //
        setCategoriaId(0)
      }
    })

    uppyIcon.on('error', (result: any) => {
      toaster('Error al subir icono de la categoría')
    })

    uppyIcon.on('file-added', (file: any) => {
      const reader = new FileReader()
      reader.readAsDataURL(file.data)
      reader.onload = function () {
        setCategoriaIcono(reader.result as string)
      }
    })

    return () => uppyIcon.close()
  }, [uppyIcon])


  const obtenerConfiguracionArbol = (id: any) => {
    setLoading(true)
    rootStore.environment.api.categoriasConfiguracion(id).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        if (!id) {
          const categorias_formateadas: any[] = []
          setCategoriasTree(result.categorias_configuracion)
          result.categorias_configuracion.map((categoria: any) => {
            const cat = { ...categoria }
            const cat_item_mod_array: any[] = []
            cat.label = `${cat.label.split('-_-')[0]} (${categoria.productos})`
            cat.items.map((cat_in: any) => {
              const cat_item_mod = { ...cat_in }
              cat_item_mod.label = cat.label.split('-_-')[0]
              cat_item_mod_array.push(cat_item_mod)
            })
            cat.items = cat_item_mod_array
            categorias_formateadas.push(cat)
          })
          setCategoriasTreeMostrar(categorias_formateadas)
        } else {
          const categorias_formateadas = [...CategoriasTreeMostrar]
          const categorias = [...CategoriasTree]
          result.categorias_configuracion.map((categoria: any) => {
            let foundValue = categorias_formateadas.filter((obj) => obj.id === categoria.id)[0]
            let foundValueCategorias = categorias.filter((obj) => obj.id === categoria.id)[0]
            const cat = { ...categoria }
            const cat_item_mod_array: any[] = []
            if (!foundValue) {
              cat.label = `${cat.label.split('-_-')[0]} (${categoria.productos})`
              cat.items.map((cat_in: any) => {
                const cat_item_mod = { ...cat_in }
                cat_item_mod.label = cat.label.split('-_-')[0]
                cat_item_mod_array.push(cat_item_mod)
              })
              cat.items = cat_item_mod_array
              categorias_formateadas.push(cat)
            }
            if (!foundValueCategorias) {
              categorias.push(categoria)
            }
          })
          setCategoriasTree(categorias)
          setCategoriasTreeMostrar(categorias_formateadas)
        }
      } else {
        apiToaster(result, '', 'leer categorias')
      }
    })
  }

  const onSelect = (selectedNode: NodeId[]) => {
    if (!selectedNode || !selectedNode.length) {
      return
    }
    const idCategoria = selectedNode[0] < 0 ? (selectedNode[0] as number) * -1 : selectedNode[0]
    const categoriaSeleccionada = CategoriasTree.find((cat) => cat.id === idCategoria)
    if (selectedNode[0] > 0 && categoriaSeleccionada.hasOwnProperty('items')) {
      // Tiene subcategorías
      obtenerConfiguracionArbol(selectedNode[0])
    } else {
      const item = categoriaSeleccionada.label.split('-_-')
      const porcentaje = parseFloat(item[1])
      let nombre = item[0]
      const apto_menores = JSON.parse(item[2])
      let tipo_iva = categoriaSeleccionada.tipo_iva ? categoriaSeleccionada.tipo_iva : 10
      setCategoriaNombre(nombre.trim())
      setCategoriaComision(porcentaje)
      setCategoriaNoAptoMenores(apto_menores)
      setCategoriaId(selectedNode[0] as number)
      setCategoriaImagen(categoriaSeleccionada.imagen)
      setCategoriaIcono(categoriaSeleccionada.icono)
      setCategoriaTipoIva(tipo_iva)
      setCategoriaEliminarImagen(false)
      setCategoriaEliminarIcono(false)
      setShowModal(true)
    }
  }

  const reloadTree = () => {
    setCategoriasTree([])
    obtenerConfiguracionArbol(null)
  }

  const handleModificar = () => {
    if (Loading) {
      return
    }
    setShowAlert(true)
  }

  const handleEliminar = () => {
    if (Loading) {
      return
    }
    setCategoriaEliminarId(CategoriaId)
    setShowAlert(true)
  }

  const modificarCategoria = () => {
    if (!CategoriaNombre || !CategoriaComision || !CategoriaTipoIva) {
      return
    }
    setLoading(true)
    if (CategoriaEliminarId) {
      rootStore.environment.api.eliminarCategoria(Math.abs(CategoriaEliminarId)).then((result) => {
        setLoading(false)
        setCategoriaEliminarId(null)
        if (result.kind === 'ok') {
          apiToaster(result, 'La categoría se eliminó exitosamente', 'eliminar categoria')
        } else {
          apiToaster(result, 'Ocurrió un error al eliminar la categoría', 'eliminar categoria')
        }
        setShowModal(false)
        reloadTree()
      })
    } else if (!CategoriaId) {
      const newCat = {
        nombre: CategoriaNombre,
        comision: CategoriaComision,
        tipo_iva: CategoriaTipoIva,
        superior: CategoriaPadreId,
        no_apto_menores: CategoriaNoAptoMenores,
      }
      rootStore.environment.api.guardarCategoria(newCat).then((result) => {
        setLoading(false)
        if (result.kind === 'ok') {
          apiToaster(result, 'La categoría se creó exitosamente', 'guardar categoria')
          // Actualizar endpoint
          setCategoriaId(result.response.id)
          // Subir imagen
          if (CategoriaImagen && CategoriaImagen.startsWith('data:')) {
            setTimeout(() => uppy.upload(), 500)
          }
          // Subir icono
          if (CategoriaIcono && CategoriaIcono.startsWith('data:')) {
            setTimeout(() => uppyIcon.upload(), 300)
          }
        } else {
          apiToaster(result, 'Ocurrió un error al crear la categoría', 'guardar categoria')
        }
        setShowModal(false)
      })
    } else {
      const id_categoria = Math.abs(CategoriaId)
      rootStore.environment.api
        .modificarCategoriaConfiguracion(
            id_categoria,
            CategoriaNombre,
            CategoriaComision,
            CategoriaTipoIva,
            CategoriaNoAptoMenores,
            CategoriaEliminarImagen,
            CategoriaEliminarIcono
        )
        .then((result) => {
          if (result.kind === 'ok') {
            setCategoriasTree(
              CategoriasTree.map((c) => {
                const categoria = c as CategoriaConfig
                if (categoria.id === id_categoria) {
                  categoria.items[0].label =
                    CategoriaNombre + '-_-' + CategoriaComision + '-_-' + JSON.parse(String(CategoriaNoAptoMenores))
                  categoria.label =
                    CategoriaNombre + '-_-' + CategoriaComision + '-_-' + JSON.parse(String(CategoriaNoAptoMenores))
                  categoria.tipo_iva = CategoriaTipoIva
                }
                if (CategoriaEliminarImagen) {
                  c.imagen = null
                }
                if (CategoriaEliminarIcono) {
                  c.icono = null
                }
                return categoria
              }),
            )
            setCategoriasTreeMostrar(
              CategoriasTreeMostrar.map((c) => {
                const categoria = c as CategoriaConfig
                if (categoria.id === id_categoria) {
                  categoria.items[0].label = CategoriaNombre
                  categoria.label = `${CategoriaNombre} (${categoria.productos})`
                }
                if (CategoriaEliminarImagen) {
                  c.imagen = null
                }
                if (CategoriaEliminarIcono) {
                  c.icono = null
                }
                return categoria
              }),
            )
            // Subir imagen
            if (CategoriaImagen && CategoriaImagen.startsWith('data:')) {
              uppy.upload()
            }

            // Subir icono
            if (CategoriaIcono && CategoriaIcono.startsWith('data:')) {
              uppyIcon.upload()
            }
          } else {
            apiToaster(result, '', 'guardar categorias')
          }
          setLoading(false)
          setShowModal(false)
        })
    }
  }

  const initCrear = () => {
    setCategoriaId(null)
    setCategoriaNombre('')
    setCategoriaComision(0)
    setCategoriaTipoIva(10)
    setCategoriaNoAptoMenores(false)
    setCategoriaPadreId(null)
    rootStore.environment.api.categorias().then((result) => {
      if (result.kind === 'ok') {
        setCategoriasList(result.categorias)
        setShowModal(true)
      } else {
        apiToaster(result, '', 'leer categorías')
      }
    })
  }

  return (
    <Page login_required admin>
      <IonAlert
        isOpen={showAlert}
        onDidDismiss={() => setShowAlert(false)}
        header={'Confirmación'}
        message={`Confirma que desea ${
          CategoriaId ? (CategoriaEliminarId ? 'eliminar' : 'modificar') : 'crear'
        } la categoría "${CategoriaNombre}"?`}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'btn-secondary',
          },
          {
            text: 'Aceptar',
            cssClass: 'btn-primary',
            handler: () => {
              modificarCategoria()
            },
          },
        ]}
      />

      <IonModal isOpen={showModal} backdropDismiss={false}>
        <IonHeader>
          <IonToolbar>
            <IonTitle>{CategoriaId ? 'Modificar' : 'Crear'} categoría</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            {!CategoriaId && (
              <IonItem>
                <IonLabel position="stacked">Categoria padre</IonLabel>
                <IonSelect
                  value={CategoriaPadreId}
                  placeholder="Elegir padre"
                  onIonChange={(e) => setCategoriaPadreId(e.detail.value)}
                >
                  <IonSelectOption value={null}>Raiz</IonSelectOption>
                  {CategoriasList.map((cat) => (
                    <IonSelectOption key={cat.id} value={cat.id}>
                      {cat.nombre}
                    </IonSelectOption>
                  ))}
                </IonSelect>
              </IonItem>
            )}
            <IonItem>
              <IonLabel position="stacked">Nombre de la categoría</IonLabel>
              <IonInput value={CategoriaNombre} onIonChange={(e) => setCategoriaNombre(e.detail.value!)} />
            </IonItem>
            <IonItem>
              <IonLabel position="stacked">Margen de la categoría</IonLabel>
              <IonInput
                type="number"
                value={CategoriaComision}
                placeholder="Ingrese el porcentaje"
                min={'0'}
                onIonChange={(e) => setCategoriaComision(parseFloat(e.detail.value!))}
              />
            </IonItem>
            <IonItem>
              <IonLabel position="stacked">Tipo IVA</IonLabel>
              <IonSelect
                  value={CategoriaTipoIva}
                  onIonChange={e => setCategoriaTipoIva(e.detail.value)}
                  okText={'Aceptar'}
                  cancelText={'Cancelar'}
              >
                {tiposIva.map((option, i) => (
                    <IonSelectOption value={option.value} key={i}>
                      {option.label}
                    </IonSelectOption>
                ))}
              </IonSelect>
            </IonItem>

            <IonItem>
              <IonLabel position="stacked">Imagen de la Categoría</IonLabel>
              {!!CategoriaImagen && (
                <div>
                  <IonImg style={{ height: 200, margin: 10 }} src={CategoriaImagen} />
                </div>
              )}
              <div className="row">
                <div className="col-12 col-md-6 mt-3 mb-3">
                  <FileInput uppy={uppy} pretty  />
                </div>
                {!!CategoriaImagen && (
                  <div className={'col-12 col-md-6'}>
                    <button
                      className={'btn btn-primary btn-block'}
                      disabled={Loading}
                      onClick={() => {
                        setCategoriaEliminarImagen(true)
                        setCategoriaImagen(null)
                      }}
                    >
                      Eliminar Imagen
                    </button>
                  </div>
                )}
              </div>
            </IonItem>
            <IonItem>
              <IonLabel position="stacked">Icono de la Categoría</IonLabel>
              {!!CategoriaIcono && (
                  <div>
                    <IonImg style={{ height: 200, margin: 10 }} src={CategoriaIcono} />
                  </div>
              )}
              <div className="row">
                <div className="col-12 col-md-6 mt-3 mb-3">
                  <FileInput uppy={uppyIcon} pretty  />
                </div>
                {!!CategoriaIcono && (
                    <div className={'col-12 col-md-6'}>
                      <button
                          className={'btn btn-primary btn-block'}
                          disabled={Loading}
                          onClick={() => {
                            setCategoriaEliminarIcono(true)
                            setCategoriaIcono(null)
                          }}
                      >
                        Eliminar Icono
                      </button>
                    </div>
                )}
              </div>
            </IonItem>
            <IonItem>
              <IonLabel position="stacked">No apto para menores de edad</IonLabel>
              <IonCheckbox
                checked={CategoriaNoAptoMenores}
                onClick={() => {
                  setCategoriaNoAptoMenores(!CategoriaNoAptoMenores)
                }}
              />
            </IonItem>
          </IonList>
        </IonContent>
        <div style={{ padding: 10 }}>
          <div className={'row'}>
            {CategoriaId && <>
              <div className={'col-6 mb-2'}>
                <a
                  className={'btn btn-primary btn-block'}
                  target={"_blank"}
                  href={`${RUTAS.ADMIN_PRODUCTO('moderado')}?c=${Math.abs(CategoriaId || 0)}`}
                >
                  Ver productos
                </a>
              </div>
              <div className={'col-6 mb-2'}>
                <button
                  className={'btn btn-danger btn-block'}
                  disabled={Loading}
                  onClick={() => {
                    handleEliminar()
                  }}
                >
                  Eliminar categoría
                </button>
              </div>
            </>}
            <div className={'col-12 col-md-6 mb-xs-2'}>
              <button className={'btn btn-secondary btn-block'} onClick={() => setShowModal(false)}>
                Cancelar
              </button>
            </div>
            <div className={'col-12 col-md-6'}>
              <button
                className={'btn btn-primary btn-block'}
                disabled={Loading}
                onClick={() => {
                  handleModificar()
                }}
              >
                Guardar
              </button>
            </div>
          </div>
        </div>
      </IonModal>

      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Categorias</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={openPage(RUTAS.ADMIN)}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Categorías</li>
              </ol>
            </div>
          </div>
          <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
            <li className="nav-item ml-auto">
              <button
                className="btn btn-primary d-flex align-items-center px-3"
                id="add-categoria"
                onClick={() => initCrear()}
              >
                Crear Categoría
              </button>
            </li>
          </ul>
          <div className="card mb-0">
            <div className="card-body">
              <div className="col-lg-12">
                <h4 className="card-title">Arbol de Categorías</h4>
                <h6 className="card-subtitle">Tabla de configuración de margen de la plataforma por categoría</h6>
                <div style={{ display: 'flex', flexWrap: 'nowrap', flexGrow: 1 }}>
                  <div style={{ width: '50%', display: 'flex', flexDirection: 'column' }}>
                    <Tree
                      nodes={CategoriasTreeMostrar}
                      showEmptyItems
                      theme={'estilo'}
                      // @ts-ignore
                      customTheme={estiloArbolCategorias}
                      onSelect={onSelect}
                      size={'full'}
                      isLoading={(!CategoriasTreeMostrar.length || showModal) && Loading}
                      loadingString={'Cargando..'}
                      emptyItemsString={'No posee subcategorías'}
                      noDataString={'No existen categorías'}
                      grow
                      iconSet={{
                        file: <FIcon.Edit3 size={32} />,
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminCategoria
